
/* -------------------- STYLES (FIGMA SPEC) -------------------- */
const styles = {
  container: { 
    display: "flex",
    width: 1920,
    flexDirection: "column",
    alignItems: "center",
    gap: 32,
    background: "#F5F5F5",
    margin: "0 auto", 
    padding: 12, 
    fontFamily: "Segoe UI", 
    boxSizing: "border-box" 
  },
  header: {
    width: 1200,
    color: "#242424",
    padding: "14px 12px",
    borderRadius: 8,
    fontWeight: 600,
    fontSize: 32,
    marginBottom: 32,
    whiteSpace: "nowrap",
    overflow: "hidden",
    textOverflow: "ellipsis",
  },
  overviewCard: { 
    width: 1200,
    background: "#ffffff",
    borderRadius: 4,
    border: "1px solid #E1DFDD",
    boxShadow: "0 1px 6px rgba(16,24,40,0.06)",
    padding: 24,
    boxSizing: "border-box",
    display: "flex",
    flexDirection: "column",
    gap: 16
  },

  overviewGrid: {
    display: "grid",
    gridTemplateColumns: "1fr 1fr 1fr 1fr",
    gap: 16,
    alignItems: "start",
    minHeight: 64,
  },

  overviewLabel: {
    fontSize: 12,
    color: "#616161",
    marginBottom: 8,
    display: "inline-block",
    minWidth: 120,
  },
  overviewValue: {
    fontSize: 13,
    color: "#242424",
    fontWeight: 600,
    display: "inline-block",
    marginLeft: 8,
  },
  overviewLink: {
    color: "#4F6BED",
    textDecoration: "none",
    cursor: "pointer",
    fontWeight: 600,
  },

  overviewDateInput: {
    padding: "10px 12px",
    borderRadius: 6,
    border: "1px solid #D1D1D1",
    background: "#fff",
    fontSize: 13,
    marginLeft: 8,
  },

  memberRoleInput: {
    padding: "10px 12px",
    borderRadius: 6,
    border: "1px solid #D1D1D1",
    fontSize: 13,
    marginLeft: 8,
    width: "60%",
  },

  panel: {
    display: "flex",
    width: 1200,
    padding: 24,
    flexDirection: "row",
    alignItems: "flex-start",
    gap: 24,
    background: "#ffffff",
    borderRadius: 4,
    border: "1px solid #E1DFDD",
    boxShadow: "0 1px 6px rgba(16,24,40,0.06)",
    boxSizing: "border-box",
  },

  input: { 
    width: "100%", 
    padding: "12px", 
    borderRadius: 6, 
    border: "1px solid #D1D1D1", 
    boxSizing: "border-box",
    fontSize: 14,
    fontFamily: "Segoe UI",
  },

  // left nav
  leftNav: {
    display: "flex",
    width: 240,
    height: "auto",
    flexDirection: "column",
    alignItems: "flex-start",
    gap: 0,
    background: "transparent",
    padding: 0,
    borderRadius: 0,
    boxShadow: "none",
    overflow: "visible",
    borderRight: "none",
    boxSizing: "border-box",
  },

  sectionListTitle: {
    fontSize: 16,
    color: "#242424",
    padding: "16px 0 8px 0",
    marginBottom: 0,
    fontWeight: 600,
  },

  leftBtn: (active) => ({
    display: "flex",
    width: "100%",
    textAlign: "left",
    fontFamily: "Segoe UI",
    justifyContent: "flex-start",
    alignItems: "center",
    padding: "8px 0 8px 12px",
    paddingRight: 12,
    borderRadius: 0,
    border: "none",
    borderRight: active ? "4px solid #4F6BED" : "none",
    background: active ? "#E8EAF6" : "transparent",
    color: active ? "#242424" : "#424242",
    fontWeight: 400,
    marginBottom: 0,
    cursor: "pointer",
    boxSizing: "border-box",
  }),

  leftBtnText: {
    display: "block",
    whiteSpace: "normal",
    wordBreak: "break-word",
    lineHeight: 1.25,
    fontSize: 14,
  },

  pageHeight: 1040,

  // RIGHT PANEL
  rightCard: (pageHeight) => ({
    display: "flex",
    width: 884,
    height: "auto",
    minHeight: 802,
    maxHeight: "none",
    padding: 24,
    flexDirection: "column",
    alignItems: "flex-start",
    gap: 24,
    borderRadius: "0 0 4px 4px",
    background: "#fff",
    boxSizing: "border-box",
    fontFamily: "Segoe UI",
    overflow: "visible",
  }),

  badgeRail: {
    display: "flex",
    gap: 0,
    padding: 0,
    marginBottom: 24,
    alignItems: "center",
    flexWrap: "nowrap",
    whiteSpace: "nowrap",
    overflowX: "visible",
  },

  badge: (active, saved, incomplete = false) => {
    const base = {
      width: 32,
      height: 32,
      minWidth: 32,
      borderRadius: 16,
      display: "inline-flex",
      alignItems: "center",
      justifyContent: "center",
      border: "2px solid",
      cursor: "pointer",
      fontWeight: 600,
      flex: "0 0 auto",
      fontSize: 14,
      boxSizing: "border-box",
      margin: 0,
    };
    if (active) return { ...base, background: "#4F6BED", color: "#fff", borderColor: "#4F6BED" };
    if (incomplete) return { ...base, background: "#FDF3E7", color: "#BC4B09", borderColor: "#F3C29B" };
    if (saved) return { ...base, background: "#F1FAF1", color: "#0F7B0F", borderColor: "#9FD89F" };
    return { ...base, background: "#fff", color: "#616161", borderColor: "#E1DFDD" };
  },

  connector: {
    flex: "0 0 auto",
    width: 16,
    height: 1,
    background: "#E1DFDD",
    alignSelf: "center",
    borderRadius: 0,
    margin: 0,
    boxSizing: "border-box",
  },

  // QUESTION CARD wrapper
  questionCard: {
    borderRadius: 4,
    borderTopLeftRadius: 4,
    borderTopRightRadius: 4,
    border: "1px solid #E1DFDD",
    background: "#fff",
    overflow: "hidden",
    marginBottom: 24,
    boxSizing: "border-box",
  },

  // Header bar of the question container
  qHeader: {
    background: "#4F6BED",
    color: "#FFFFFF",
    padding: "12px 24px",
    fontWeight: 600,
    borderTopLeftRadius: 4,
    borderTopRightRadius: 4,
    fontFamily: "Segoe UI",
    fontSize: 20,
    lineHeight: "26px",
    display: "flex",
    alignItems: "center",
  },

  // qInner padding is set to 24px
  qInner: {
    padding: "24px",
    display: "flex",
    flexDirection: "column",
    gap: 16,
    boxSizing: "border-box",
    background: "#fff",
  },

  // Option row
  optionRow: (checked) => ({
    display: "flex",
    gap: 12,
    alignItems: "flex-start",
    padding: "12px 16px",
    borderRadius: 4,
    border: "1px solid",
    borderColor: checked ? "#4F6BED" : "#E1DFDD",
    background: "#fff",
    cursor: "pointer",
    marginBottom: 0,
    width: "100%",
    boxSizing: "border-box",
    minHeight: 48,
  }),

  // Plain numeric label
  optionNumber: (hasNumber, checked) => ({
    width: 20,
    minWidth: 20,
    textAlign: "center",
    color: "#242424",
    fontWeight: 600,
    fontSize: 16,
    lineHeight: "20px",
    alignSelf: "flex-start",
    marginRight: 12,
    marginTop: 2,
  }),

  visibleRadio: {
    width: 20,
    height: 20,
    marginTop: 2,
    marginRight: 0,
    cursor: "pointer",
    accentColor: "#4F6BED",
    alignSelf: "flex-start",
  },

  optionBody: {
    flex: 1,
    display: "flex",
    flexDirection: "column",
    gap: 4,
    color: "#242424",
    fontSize: 14,
    lineHeight: "20px",
  },

  describeArea: { 
    width: "100%", 
    minHeight: 80, 
    maxHeight: 320, 
    padding: 12, 
    borderRadius: 6, 
    border: "1px solid #D1D1D1", 
    boxSizing: "border-box", 
    resize: "vertical", 
    fontFamily: "Segoe UI",
    fontSize: 14,
  },

  pageFooter: { display: "flex", alignItems: "center", justifyContent: "center", gap: 16, marginTop: 32, flexShrink: 0 },
  pageSaveRow: { display: "flex", justifyContent: "center", gap: 16, alignItems: "center", flex: "0 0 auto" },
  bottomNav: { display: "flex", justifyContent: "flex-end", gap: 16, alignItems: "center", width: "100%" },
  
  btn: { 
    height: 40,
    padding: "12px 20px", 
    borderRadius: 6, 
    border: "1px solid #4F6BED", 
    background: "#FFFFFF", 
    color: "#4F6BED",
    cursor: "pointer",
    fontFamily: "Segoe UI",
    fontSize: 14,
    fontWeight: 600,
  },
  
  btnPrimary: { 
    height: 40,
    padding: "12px 20px", 
    borderRadius: 6, 
    border: "none", 
    background: "#4F6BED", 
    color: "#FFFFFF", 
    cursor: "pointer",
    fontFamily: "Segoe UI",
    fontSize: 14,
    fontWeight: 600,
  },
  
  disabledBtn: { opacity: 0.5, cursor: "not-allowed" },

  sectionBadge: (completed, incomplete = false) => ({
    display: "inline-flex",
    alignItems: "center",
    justifyContent: "center",
    width: 20,
    height: 20,
    borderRadius: 10,
    background: completed ? "#0F7B0F" : incomplete ? "#BC4B09" : "transparent",
    color: completed ? "#fff" : incomplete ? "#fff" : "#A19F9D",
    border: completed || incomplete ? "none" : "1px solid transparent",
    fontSize: 12,
    fontWeight: 700,
    marginRight: 8,
    flex: "0 0 auto",
  }),

  // Confirmation modal styles
  confirmOverlay: {
    position: "fixed",
    inset: 0,
    background: "rgba(0,0,0,0.4)",
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
    zIndex: 9999,
  },
  confirmBox: {
    width: 420,
    maxWidth: "90%",
    background: "#fff",
    borderRadius: 8,
    padding: 24,
    boxShadow: "0 6px 24px rgba(0,0,0,0.2)",
    boxSizing: "border-box",
  },
  confirmButtons: {
    display: "flex",
    justifyContent: "flex-end",
    gap: 16,
    marginTop: 24,
  },
};



export default styles;
