import React from "react";

const ReviewScreen = ({ onBack, onSubmit, isSaving }) => {
  const styles = {
    container: {
      display: "flex",
      flexDirection: "column",
      alignItems: "flex-start",
      justifyContent: "flex-start",
      padding: 48,
      height: "100%",
      width: "100%",
      textAlign: "left",
      boxSizing: "border-box",
    },
    title: {
      fontSize: 24,
      fontWeight: 600,
      color: "#242424",
      marginBottom: 24,
    },
    message: {
      fontSize: 14,
      color: "#616161",
      lineHeight: 1.6,
      marginBottom: 32,
    },
    buttonGroup: {
      display: "flex",
      gap: 12,
      justifyContent: "flex-end",
      width: "100%",
    },
    buttonSecondary: {
      height: 40,
      padding: "12px 20px",
      borderRadius: 6,
      border: "1px solid #4F6BED",
      background: "#fff",
      color: "#4F6BED",
      fontSize: 14,
      fontWeight: 600,
      cursor: "pointer",
      fontFamily: "Segoe UI",
    },
    buttonPrimary: {
      height: 40,
      padding: "12px 20px",
      borderRadius: 6,
      border: "none",
      background: "#4F6BED",
      color: "#fff",
      fontSize: 14,
      fontWeight: 600,
      cursor: "pointer",
      fontFamily: "Segoe UI",
    },
  };

  return (
    <div style={styles.container}>
      <h1 style={styles.title}>One last step...</h1>
      
      <p style={styles.message}>
        You've completed filling in the form.
        <br /><br />
        If you are comfortable with your entries, please click the "Submit" button to<br />
        complete the process.
      </p>

      <div style={styles.buttonGroup}>
        <button style={styles.buttonSecondary} onClick={onBack} disabled={isSaving}>
          Review or update your entries
        </button>
        <button style={styles.buttonPrimary} onClick={onSubmit} disabled={isSaving}>
          {isSaving ? "Submitting..." : "Submit"}
        </button>
      </div>
    </div>
  );
};

export default ReviewScreen;
